@import '../util/vaiable';
@import '../util/mixin';

$background: map-get($colors, 'background-white');
$header-height: map-get($size, 'header-height');

.body {
  min-height: calc(100vh - #{$header-height});
  box-sizing: border-box;
  padding: 20px 0px;
  background-color: $background;

  @include width-with-margin(100%, 16px, 16px);

  @include respond-to('small') {
    @include width-with-margin(100%, 32px, 32px);
  }

  @include respond-to('regular') {
    @include width-with-center(840px);
  }

  @include respond-to('medium') {
    @include width-with-margin(100%, 200px, 200px);
  }

  @include respond-to('large') {
    @include width-with-center(1040px);
  }
}
